import {
  AfterViewInit,
  Directive,
  ElementRef,
  Inject,
  Input,
  NgZone,
  OnDestroy,
  Optional,
  ViewContainerRef
} from '@angular/core';
import {
  _MatTooltipBase,
  MAT_TOOLTIP_DEFAULT_OPTIONS,
  MAT_TOOLTIP_SCROLL_STRATEGY,
  MatTooltipDefaultOptions
} from '@angular/material/tooltip';
import { Overlay, ScrollDispatcher } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import { AriaDescriber, FocusMonitor } from '@angular/cdk/a11y';
import { Directionality } from '@angular/cdk/bidi';
import { DOCUMENT } from '@angular/common';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';

import { DscTooltipComponent } from './dsc-tooltip.component';

export type DscTooltipPosition = 'after' | 'before' | 'above' | 'below' | 'left' | 'right';

export type DscTooltipVariant = 'highlight' | 'neutral';

@Directive({
  selector: '[dscTooltip]'
})
export class DscTooltipDirective extends _MatTooltipBase<DscTooltipComponent> implements AfterViewInit, OnDestroy {
  protected override readonly _tooltipComponent = DscTooltipComponent;
  protected override readonly _cssClassPrefix = 'mat-mdc';

  @Input('dscTooltipPosition')
  get placement(): DscTooltipPosition {
    return this._placement;
  }

  set placement(value: DscTooltipPosition) {
    this._placement = value ?? 'above';
    super.position = this._placement;
  }

  private _placement!: DscTooltipPosition;

  @Input('dscTooltipVariant')
  get variant(): DscTooltipVariant {
    return this._variant;
  }

  set variant(value: DscTooltipVariant) {
    this._variant = value;
    super.tooltipClass = {
      'mat-tooltip--highlight': this.variant === 'highlight'
    };
  }

  private _variant: DscTooltipVariant = 'neutral';

  @Input('dscTooltipPositionAtOrigin')
  override get positionAtOrigin(): boolean {
    return super.positionAtOrigin;
  }

  override set positionAtOrigin(value: BooleanInput) {
    super.positionAtOrigin = value;
  }

  @Input('dscTooltipDisabled')
  override get disabled(): boolean {
    return super.disabled;
  }

  override set disabled(value: BooleanInput) {
    super.disabled = value;
  }

  @Input('dscTooltipShowDelay')
  override get showDelay(): number {
    return super.showDelay;
  }

  override set showDelay(value: NumberInput) {
    super.showDelay = value;
  }

  @Input('dscTooltipHideDelay')
  override get hideDelay(): number {
    return super.hideDelay;
  }

  override set hideDelay(value: NumberInput) {
    super.hideDelay = value;
  }

  @Input('dscTooltip')
  override get message() {
    return super.message;
  }

  override set message(value: string) {
    super.message = value;
  }

  constructor(
    overlay: Overlay,
    elementRef: ElementRef<HTMLElement>,
    scrollDispatcher: ScrollDispatcher,
    viewContainerRef: ViewContainerRef,
    ngZone: NgZone,
    platform: Platform,
    ariaDescriber: AriaDescriber,
    focusMonitor: FocusMonitor,
    @Inject(MAT_TOOLTIP_SCROLL_STRATEGY) scrollStrategy: any,
    @Optional() dir: Directionality,
    @Optional() @Inject(MAT_TOOLTIP_DEFAULT_OPTIONS) defaultOptions: MatTooltipDefaultOptions,
    @Inject(DOCUMENT) _document: any,
  ) {
    super(
      overlay,
      elementRef,
      scrollDispatcher,
      viewContainerRef,
      ngZone,
      platform,
      ariaDescriber,
      focusMonitor,
      scrollStrategy,
      dir,
      defaultOptions,
      _document
    );
    this._viewportMargin = 8;
    super.position = 'above';
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
  }
}

