import {
  ANIMATION_MODULE_TYPE,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Inject,
  Optional,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { _TooltipComponentBase } from '@angular/material/tooltip';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';

import { Observable } from 'rxjs';

const MIN_HEIGHT = 24;

const MAX_WIDTH = 200;

@Component({
  selector: 'dsc-tooltip',
  templateUrl: './dsc-tooltip.component.html',
  styleUrls: ['./dsc-tooltip.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[style.zoom]': 'isVisible() ? 1 : null',
    '(mouseleave)': '_handleMouseLeave($event)',
    'aria-hidden': 'true',
  }
})
export class DscTooltipComponent extends _TooltipComponentBase {
  @ViewChild('tooltip', { static: true })
  override _tooltip!: ElementRef<HTMLElement>;

  override _showAnimation = 'mat-mdc-tooltip-show';

  override _hideAnimation = 'mat-mdc-tooltip-hide';

  _isHandset: Observable<BreakpointState>;

  _isMultiline = false;

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    breakpointObserver: BreakpointObserver,
    private _elementRef: ElementRef<HTMLElement>,
    @Optional() @Inject(ANIMATION_MODULE_TYPE) animationMode?: string,
  ) {
    super(changeDetectorRef, animationMode);
    this._isHandset = breakpointObserver.observe(Breakpoints.Handset);
  }

  protected override _onShow(): void {
    this._isMultiline = this._isTooltipMultiline();
    this._markForCheck();
  }

  private _isTooltipMultiline() {
    const rect = this._elementRef.nativeElement.getBoundingClientRect();
    return rect.height > MIN_HEIGHT && rect.width >= MAX_WIDTH;
  }
}
